<template>
<div>
	<nav-header></nav-header>
	 <div class="container">
    <div class="page-title-normal">
      <h2 class="page-title-h2"><span></span></h2>
    </div>
    <!--  -->
    <div class="check-step">
        <ul>
          <li class="cur"><span></span></li>
          <li class="cur"><span></span></li>
          <li class="cur"><span></span></li>
          <li class="cur"><span></span></li>
        </ul>
    </div>

    <div class="order-create">
      <div class="order-create-pic"><img src="../../static/ok-2.png" alt=""></div>
      <div class="order-create-main">
        <h3><br>1-7</h3>
        <p>
          <span>:{{orderId}}</span>
          <span>:{{orderTotal|currency('&yen')}}</span>
        </p>
        <div class="order-create-btn-wrap">
          <div class="btn-l-wrap">
            <router-link  class="btn btn--m" to="/cart"></router-link>

          </div>
          <div class="btn-r-wrap">
                <router-link  class="btn btn--m" to="/GoodsList"></router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
    <nav-footer></nav-footer>
 </div> 
</template>
<script>
import NavHeader from '@/components/Header'
import NavFooter from '@/components/Footer'
import '../assets/css/checkout.css'
import axios from 'axios'
  export default{
        data(){
            return{
        		  orderId:'',
        		  orderTotal:0
            }
        },
         components: { 
          NavHeader,
          NavFooter,
        },
         mounted(){
          //
         	var orderId=this.$route.query.orderId;
         	if(!orderId){
         		alert('ID');
         		return false;
         	}
          //
         	axios.get('/users/orderDetail',{params:{orderId:orderId}}).then((respone)=>{
         		  let res =respone.data;
              if(res.status=='0'){
                 this.orderId=res.result.orderId
                  this.orderTotal=res.result.orderTotal
              }else{
                  //
                  this.$router.push({
                    path:'/'
                   })
              } 
         	})
        },
    }
</script>


